<template>
  <div class="resume-working">
    <v-carousel :show-arrows="false" v-model="model" height="97%">
      <v-carousel-item :value="0"
        reverse-transition="fade-transition"
        transition="fade-transition">
        <work-time-line @cardClick="cardClick"></work-time-line>
      </v-carousel-item>
      <v-carousel-item
        v-for="(item, i) in projects"
        :value="i+1"
        reverse-transition="fade-transition"
        transition="fade-transition">
        <project-detail
          :project="item"
          :page="i+1"
          :total="total"
          @nextProject="nextProject"
          @backToHome="backToHome"
          @previousProject="previousProject"
        ></project-detail>
      </v-carousel-item>
    </v-carousel>
  </div>
</template>

<script>
import CONFIG from '@/config/personalInfo.js';
import workTimeLine from '@/components/resume/workTimeLine.vue';
import projectDetail from '@/components/resume/projectDetail.vue';

export default {
  components: {
    workTimeLine,
    projectDetail
  },
  data () {
    return {
      model:0,
      projects: CONFIG.recentProjects,
    }
  },
  computed: { 
    total: function () {
      if(CONFIG.recentProjects){
        return CONFIG.recentProjects.length
      }
      return 0
    }
  },
  methods: {
    cardClick(index){
      this.model = index
    },
    previousProject(index){
      if(index > 0){
        this.model = index - 1
      }
    },
    nextProject(index) {
      if(index === this.total){
        return
      }else{
        this.model = index + 1
      }
    },
    backToHome(){
      this.model = 0
    }
  }
}
</script>

<style lang="stylus" scoped>
.resume-working
  top 0
  left 0
  bottom 0
  width 100%
  height  100%
  overflow auto
  position relative
  background-size cover
  background linear-gradient(rgb(69, 69, 70) 0%, rgb(0, 0, 0) 100%)

</style>
