<template>
  <v-card
    height="100%"
    max-width = "220"
    flat tile
    class="blogger-home-card"
  >
    <v-navigation-drawer
      v-model="drawerShow"
      :color="'#121212'"
      :mini-variant="miniVariant"
      :expand-on-hover="expandHover"
      :permanent="true" dark
      hide-overlay absolute
    >
      <v-list dense nav class="py-0" active>
        <!-- 头像 签名 座右铭 -->
        <v-list-item two-line class="px-0" link>
          <v-list-item-avatar class="blogger-avatar"><img :src="avatar"></v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title class="name-cls">{{ author }}</v-list-item-title>
            <v-list-item-subtitle><p>{{ motto }}</p></v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
        <v-divider></v-divider>
        <!-- 个人项目 快捷链接 -->
        <v-list-item-group color="success">
          <v-list-item
            v-for="(item, i) in items"
            :key="i" link
            @click="itemClick(item)"
          >
            <v-list-item-avatar height="32" width="32"><img :src="item.avatar"></v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title v-text="item.title"></v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
        <v-divider></v-divider>
        <!-- 分类 快捷链接 -->
        <v-list-group
          :value="false"
          :expand='true'
          color="success"
          prepend-icon="B"
          v-if="pythonTitleTotal || javaTitleTotal || dbTitleTotal"
          >
            <!-- 后端 -->
            <template v-slot:activator>
              <v-list-item-title>后端技巧</v-list-item-title>
            </template>
          <v-list-item v-if="pythonTitleTotal > 0" link @click="categoryClick('python')">
            <v-list-item-avatar height="32" width="32"><img :src="PYTHON" /></v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title>Python</v-list-item-title>
            </v-list-item-content>
            <v-avatar size="16" ><i class="redNum">{{ pythonTitleTotal }}</i></v-avatar>
          </v-list-item>
          <v-list-item v-if="javaTitleTotal > 0" link @click="categoryClick('java')">
            <v-list-item-avatar height="32" width="32"><img :src="JAVA" /></v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title>Java</v-list-item-title>
            </v-list-item-content>
            <v-avatar size="16" ><i class="redNum">{{ javaTitleTotal }}</i></v-avatar>
          </v-list-item>
          <v-list-item v-if="dbTitleTotal > 0" link @click="categoryClick('db')">
            <v-list-item-avatar height="32" width="32"><img :src="MONGO" /></v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title>数据库</v-list-item-title>
            </v-list-item-content>
            <v-avatar size="16" ><i class="redNum">{{ dbTitleTotal }}</i></v-avatar>
          </v-list-item>
        </v-list-group>

        <!-- 前端 -->
        <v-list-group
          :value="false"
          :expand='true'
          color="success"
          prepend-icon="F"
          v-if="vueTitleTotal || cssTitleTotal || jsTitleTotal"
          >
            <template v-slot:activator>
              <v-list-item-title>前端技能</v-list-item-title>
            </template>

          <v-list-item v-if="vueTitleTotal > 0" link @click="categoryClick('vue')">
            <v-list-item-avatar height="32" width="32"><img :src="VUE" /></v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title>Vue</v-list-item-title>
            </v-list-item-content>
            <v-avatar size="16" ><i class="redNum">{{ vueTitleTotal }}</i></v-avatar>
          </v-list-item>
          <v-list-item v-if="cssTitleTotal > 0" link @click="categoryClick('css')">
            <v-list-item-avatar height="32" width="32"><img :src="CSS" /></v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title>Css</v-list-item-title>
            </v-list-item-content>
            <v-avatar size="16" ><i class="redNum">{{ cssTitleTotal }}</i></v-avatar>
          </v-list-item>
          <v-list-item v-if="jsTitleTotal > 0" link @click="categoryClick('js')">
            <v-list-item-avatar height="32" width="32"><img :src="JS" /></v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title>JS</v-list-item-title>
            </v-list-item-content>
            <v-avatar size="16" ><i class="redNum">{{ jsTitleTotal }}</i></v-avatar>
          </v-list-item>
        </v-list-group>

        <!-- 前沿技术 -->
        <v-list-group
          :value="false"
          :expand='true'
          color="success"
          prepend-icon="A"
          v-if="bigdataTitleTotal || deeplearnTitleTotal"
          >
            <template v-slot:activator>
              <v-list-item-title>前沿技术</v-list-item-title>
            </template>

          <v-list-item v-if="bigdataTitleTotal > 0" link @click="categoryClick('bigdata')">
            <v-list-item-avatar height="32" width="32"><img :src="BIGDATA" /></v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title>大数据</v-list-item-title>
            </v-list-item-content>
            <v-avatar size="16" ><i class="redNum">{{ bigdataTitleTotal }}</i></v-avatar>
          </v-list-item>
          <v-list-item v-if="deeplearnTitleTotal > 0" link @click="categoryClick('deeplearn')">
            <v-list-item-avatar height="32" width="32"><img :src="AI" /></v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title>深度学习</v-list-item-title>
            </v-list-item-content>
            <v-avatar size="16" ><i class="redNum">{{ deeplearnTitleTotal }}</i></v-avatar>
          </v-list-item>
        </v-list-group>

        <v-divider></v-divider>
        <!-- 时间，地理位置 -->
        <v-list-item three-line>
          <v-list-item-avatar><v-avatar color="#81D4FA">{{ localCity | getFirstStr}}</v-avatar></v-avatar></v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>{{ nowTime }} </v-list-item-title>
            <v-list-item-subtitle>{{ nowDate }} {{ nowWeek }}</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>

        <v-list-item three-line v-show="todayWeather">
          <!-- 今日天气 -->
          <v-list-item-avatar height="32" width="32"><img :src="todayImg"></v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-subtitle v-html="todayWeather"> </v-list-item-subtitle>
            <v-list-item-subtitle v-html="todayTemperature"> </v-list-item-subtitle>
          </v-list-item-content>
          <!-- 明日天气 -->
          <v-list-item-avatar height="32" width="32"><img :src="tomorrowImg"></v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-subtitle v-html="tomorrowWeather"> </v-list-item-subtitle>
            <v-list-item-subtitle v-html="tomorrowTemperature"> </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
        <v-divider></v-divider>
        <v-list-item link @click="itemClick({path:'/blog/wechat'})">
          <v-list-item-avatar height="32" width="32"><img :src="PAY"></v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>打赏一下~</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </v-card>
</template>

<script>
  import CONFIG from '@/config/config.js';
  // 博文类别
  import TAG from '@/assets/imgs/nav/tag.png'
  import PAY from '@/assets/imgs/nav/pay.png'
  import FOOT from "@/assets/imgs/nav/foot.png"
  import AXIS from '@/assets/imgs/nav/axis.png'
  import MAYBE from '@/assets/imgs/nav/maybe.png'
  import LOVETALK from '@/assets/imgs/nav/lovetalk.png'
  import RESUME from '@/assets/imgs/nav/center_ic_resume.png'

  // 技术栈
  import AI from '@/assets/imgs/nav/ai.png'
  import VUE from '@/assets/imgs/nav/vue.png'
  import CSS from '@/assets/imgs/nav/css.png'
  import JAVA from '@/assets/imgs/nav/java.png'
  import JS from '@/assets/imgs/nav/Nodejs.png'
  import PYTHON from '@/assets/imgs/nav/python.png'
  import MONGO from '@/assets/imgs/nav/mongodb.png'
  import BIGDATA from '@/assets/imgs/nav/bigdata.png'

  export default {
    name: 'blogHome',
    props: {
      color:{
        type: String,
        default:"#4CAF50"
      },
    },
    data () {
      return {
        AI,
        JS,
        CSS,
        PAY,
        VUE,
        JAVA,
        MONGO,
        PYTHON, 
        BIGDATA,
        // 侧边栏设置
        drawerShow: true,
        miniVariant: true,
        expandHover:true,
        avatar:CONFIG.LOGO,
        motto:CONFIG.MOTTO,
        author:CONFIG.AUTHOR,
        title:CONFIG.TITLE,
        // 时间，天气
        _timer:null,
        todayImg:"",
        todayWeather:"",
        todayTemperature:"",
        tomorrowImg:"",
        tomorrowWeather:"",
        tomorrowTemperature:"",
        localCity: "杭州",
        localCountry:"中国",
        localToday:"",
        nowWeek:"",
        nowDate:"",
        nowTime:"",
        // 博文分类
        items:[
          {
            avatar:FOOT,
            title:"足迹",
            path:"/blog/traval"
          },
          {
            avatar:AXIS,
            title:"时间轴",
            path:"/blog/time-line"
          },
          // {
          //   avatar:TAG,
          //   title:"标签云",
          //   path:"/tags"
          // },
          {
            avatar:RESUME,
            title:"个人简历",
            path:"/about-me"
          },
          {
            avatar:LOVETALK,
            title:"LOVETALK",
            path:"/project/love-talk"
          },
          {
            avatar:MAYBE,
            title:"Maybe",
            path:"/project/maybe"
          }
        ],
        // 博文数量统计
        pythonTitleTotal:0,
        javaTitleTotal:0,
        dbTitleTotal:0,
        vueTitleTotal:0,
        cssTitleTotal:0,
        jsTitleTotal:0,
        bigdataTitleTotal:0,
        deeplearnTitleTotal:0,
      }
    },
    computed: {},
    filters: {
      getFirstStr(value){
        value = value || "杭州";
        return value.substr(0, 1)
      },
    },
    methods:{
      // 列表项点击事件
      itemClick(item){
        let url = item.path;
        if (url.startsWith("http://")){
          window.open(url, "_blank");
        }else{
          this.$router.push({path:url})
        }
      },
      // 获取日期
      getDate() {
        let _this = this;
        let yy = new Date().getFullYear();
        let mm = new Date().getMonth() + 1;
        let dd = new Date().getDate();
        let week = new Date().getDay();
        let hh = new Date().getHours();
        let _mf = new Date().getMinutes();
        let mf = _mf< 10? "0"+_mf: _mf;
        let _ss = new Date().getSeconds();
        let ss = _ss < 10? "0"+_ss: _ss;
        if (week == 1) {
            this.nowWeek = "周一";
        } else if (week == 2) {
            this.nowWeek = "周二";
        } else if (week == 3) {
            this.nowWeek = "周三";
        } else if (week == 4) {
            this.nowWeek = "周四";
        } else if (week == 5) {
            this.nowWeek = "周五";
        } else if (week == 6) {
            this.nowWeek = "周六";
        } else {
            this.nowWeek = "周日";
        }
        _this.nowDate = yy + "-" + mm + "-" + dd;
        _this.nowTime =  hh + ":" + mf + ":" + ss;
      },
      // 获取本地天气
      async getWeather() {
        let _this = this;
        _this.$http.get('https://tianqiapi.com/api?version=v1&appid=76484249&appsecret=IKj68MGP&vue=1').then(
          res => {
            console.log('一周天气：', res.data)
            _this.localCity = res.data.city || "杭州";
            _this.localCountry = res.data.country || "中国";
            let weatherData = res.data.data;
            let today = weatherData[0];
            let tomorrow = weatherData[1];
            _this.todayImg = 'http://tq.daodaoim.com//tianqiapi/skin/pitaya/' + today.wea_img +'.png';
            _this.todayWeather = '<span style="color:#EA80FC">'+today.wea+'</span>';
            _this.todayTemperature = '<span style="color:#EA80FC">'+today.tem+'</span>';
            _this.tomorrowImg = 'http://tq.daodaoim.com//tianqiapi/skin/pitaya/' + tomorrow.wea_img +'.png';
            _this.tomorrowWeather = '<span style="color:red">'+today.wea+'</span>';
            _this.tomorrowTemperature = '<span style="color:red">'+today.tem+'</span>';

        }).catch(() => {
        })

      },
      // 获取各个分类下文章数目
      async getTitleTotal(){

      },
      // 分类栏点击事件
      categoryClick(item){

      },
    },
    mounted(){
      let _this = this;
      _this.getTitleTotal();
      _this.getDate();
      _this.getWeather();
      if (_this._timer) clearInterval(_this._timer);
      this._timer = setInterval(() => {
        this.getDate();
      }, 1000);
    },
    destroyed() {
      if (this._timer) clearInterval(this._timer);
    }
  }
</script>
<style lang="stylus" scoped>
.blogger-home-card
  overflow  auto
  width 100%
  height  100%
  position absolute
  left  0
  top  0
  border none
.redNum
  color red
  font-size 8px
  size 8px
.name-cls
  margin-left 16%
  font-weight 700
</style>
