<template>
  <v-card dark width="66%" style="margin-left:16%;margin-top:20px">
    <v-toolbar flat :color="project.color" dark>
      <v-toolbar-title>{{ project.projectName }}</v-toolbar-title>
    </v-toolbar>

    <v-card-text>
      <v-row>
        <v-col>
          <v-text-field filled label="开发周期" :value="project.time"></v-text-field>
        </v-col>
        <v-col>
          <v-text-field filled label="项目角色" :value="project.projectRole"></v-text-field>
        </v-col>
      </v-row>
      
      <v-textarea auto-grow dark shaped outlined label="项目描述" :rows="2" :value="project.projectDescption"></v-textarea>
      <v-textarea auto-grow dark shaped outlined label="负责模块" :value="project.projectMoudles | cookModules"></v-textarea>

      <span v-if="project.keyPoint">
        <span v-for="(item, i) in project.keyPoint">
          <v-row v-if="item.descption">
            <v-textarea filled label="项目难点" :rows="item.rows | cookRows" :value="item.descption"></v-textarea>
            <v-textarea filled label="解决方案" :rows="item.rows | cookRows" :value="item.solution" style="margin-left:5px"></v-textarea>
          </v-row>
        </span>
      </span>

      <v-divider class="my-2"></v-divider>
      <v-item-group multiple>
        <v-subheader>项目技能</v-subheader></v-subheader>
        <v-item v-for="skill in project.projectSkill" :key="skill.name" v-slot:default="{ active, toggle }">
          <v-chip color="#1B5E20" outlined pill style="margin-right:5px">
              <p class="resume-skill-tag">{{ skill.name }}</p>
              <v-avatar v-if="skill.icon"><v-img :src="skill.icon"></v-img></v-avatar>
          </v-chip>
        </v-item>
      </v-item-group>
    </v-card-text>

    <v-divider></v-divider>

    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="#76FF03" outlined class="mx-10" @click="previousProject" v-show="previousShow">上一个</v-btn>
      <v-btn color="#FF8A65" outlined class="mx-10" @click="nextProject" v-show="nextShow">下一个</v-btn>
      <v-btn color="#EF6C00" outlined class="mx-10" @click="backToHome">近期项目</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import CONFIG from '@/config/personalInfo.js';

export default {
  name: 'personalSkill',
  data() {
    return {
      previousShow:true,
      nextShow:true
    }
  },
  props:{
    project:{
      type: Object
    },
    total: { 
      type:Number,
      default:1
    },
    page: { 
      type:Number,
      default:0 
    }
  },
  filters: { 
    cookModules(value){
      let str = "";
      if(value && (value instanceof Array)){
        for(let v of value){
          str += "    ●    " + v + "\n"
        }
      }
      return str.substr(0, str.length - 1)
    },
    cookRows(value){
      value = value || 1;
      return value
    },
    cookRoleColor(value){
      if(value == "核心开发者"){
        return "#CCFF90"
      }else if(value == "独立开发"){
        return "#FF4081"
      }else{
        return "#FFB74D"
      }
    }
  },
  methods:{
    previousProject(){
      this.$emit('previousProject', this.page)
    },
    nextProject() {
      this.$emit('nextProject', this.page)
    },
    backToHome(){
      this.$emit('backToHome')
    }
  },
  mounted() {
    let _this = this;
    _this.previousShow = true;
    _this.previousShow = true;
    if(_this.page == 1){
      _this.previousShow = false;
    }
    if(_this.page == _this.total){
      _this.nextShow = false;
    }
  }
}
</script>

<style lang="stylus" scoped>
.resume-project-detail
  top 0
  left 0
  bottom 0
  width 100%
  height  100%
  position relative
  background-size cover
  background linear-gradient(rgb(69, 69, 70) 0%, rgb(0, 0, 0) 100%)

.resume-skill-tag
  color #B2FF59
  margin-right  5px

.mx-10
  margin-right  10px
</style>
