<template>
  <div class="contact-page">
    <v-row no-gutters>
      <v-col v-for="contact in items">
        <v-card max-width="666" min-width="500" class="contact-card">
          <v-toolbar color="indigo" dark>
            <v-avatar dark size="32" color="#FFFFFF"><v-img :src="contact.icon" contain dark></v-img></v-avatar>
            <v-toolbar-title class="mx-20">{{ contact.title }}</v-toolbar-title>
          </v-toolbar>
          <v-list>
            <v-list-item
              v-for="item in contact.content"
              :key="item.title"
              @click="jumpContact(item)"
            >
              <v-list-item-avatar><v-img :src="item.avatar"></v-img></v-list-item-avatar>
              <v-list-item-title class="mx-20"><b>{{ item.title }}</b></v-list-item-title>
              <v-list-item-subtitle class="mx-10">{{ item.subTitle }}</v-list-item-subtitle>
            </v-list-item>
          </v-list>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="3" class="contact-card">
        <v-img :src="qrcode" size="64" v-if="qrcode"></v-img>
      </v-col>
    </v-row>
  </div>
</template>

<script>
  import CONFIG from '@/config/personalInfo.js'
  export default {
    data () {
      return {
        items: CONFIG.contact,
        qrcode:""
      }
    },
    methods: {
      jumpContact(item){
        let _this = this;
        let link = item.link;
        _this.qrcode = "";
        if(link){
          window.open(link, "_blank");
        }else if(item.qrcode){
          _this.qrcode = item.qrcode;
        }
      }
    }
  }
</script>

<style lang="stylus" scoped>
.contact-page
  width 100%
  height  100%
  position  relative
  bottom 0
  background-size 100% 100%
  background linear-gradient(rgb(69, 69, 70) 0%, rgb(0, 0, 0) 100%)

.contact-card
  margin-top 10%
  margin-left 16%

.mx-20
  margin-left 20px

.mx-10
  margin-left 10px
</style>