<template>
    <div :id="score" :style="{width: width, height: height}"></div>
</template>

<script>
import echarts from 'echarts'
export default {
    props: {
        width:{
            type:String,
            default:'300px'
        },
        height: {
            type:String,
            default:"300px"
        },
        title:{
            type: String,
            default:"后端"
        },
        titleColor:{
            type: String,
            default:'#18DB9F'
        },
        echartData:{
            type: Array,
        },
        img:{
            type: String,
        },
        leftItem:{
            type: Array,
        },
        rightItem:{
            type: Array,
        },
        index:{
            type:Number,
            default:0,
            required: true
        },
        colors:{},
        backgroundColor:{
            type:String,
            default:"#FFFFFF"
        }
    },
    data() {
        return {
            myChart:null,
            fontColor:'#18DB9F',
        }
    },
    computed: {
        imgSrc: function () {
           return this.src || "data:image/png;base64,"     
        },
        score: function () {
            return "score"+this.index || "score0"
        }
    },
    mounted() {
        this.initData()
    },
    methods: {
        initData() {
            let total = this.echartData.reduce((a, b) => {
                return a + b.value
            }, 0);
            let eData = this.echartData;
            let _color = this.colors || ['#06FDBC', '#F6FE05', '#07B0FE', '#FDB408', '#00DB1C', '#0188FE'];
            let legendOption = {
                top: "center",
                orient: 'vertical',
                icon: "circle",
                itemWidth: 12,
                itemGap: 13,
                textStyle: {
                    fontSize: 14,
                    rich: {
                        name: {
                            color: "#E040FB",
                            padding: [10, 5, 30, 5],
                        },
                        percent: {
                            color: this.fontColor,
                            fontSize: 18,
                            padding: [0, 5, 0, 5],
                        }
                    }
                },
                formatter: function(name) {
                    let res = eData.filter(v => v.name === name);
                    let percent = ((res[0].value * 100) / total).toFixed(1);
                    return (
                        "{name| " + name + "}\n{percent|" + percent + "%}"
                    );
                }
            };

            let option = {
                backgroundColor: this.backgroundColor,
                color: _color,
                tooltip: {
                    trigger: "item",
                    formatter(){
                        let v = "";
                        eData.map(item => {
                            v += item.name + ":" +item.value +"<br />"
                        });
                        return v
                    }
                },
                graphic: {
                    elements: [{
                        type: "image",
                        z: 3,
                        style: {
                            image: this.imgSrc,
                            width: 90,
                            height: 90
                        },
                        left: 'center',
                        top: "36%",
                        position: [100, 100]
                    }]
                },
                title: {
                    text: this.title,
                    top: '54%',
                    textAlign: "center",
                    left: "50%",
                    textStyle: {
                        color: this.titleColor,
                        fontSize: 20,
                        fontWeight: '400'
                    }
                },
                legend: [{
                    left: 60,
                    data: this.leftItem,
                    align: 'right',
                    ...legendOption
                }, {
                    right: 60,
                    data: this.rightItem,
                    align: 'left',
                    ...legendOption
                }],
                toolbox: {
                    show: false
                },
                series: [{
                    name: "",
                    type: "pie",
                    radius: [0, 150],
                    center: ["50%", "50%"],
                    label: {
                        show: false
                    },
                    itemStyle: {
                        normal: {
                            color: 'rgba(24,219,159,0.1)'
                        },
                    },
                    hoverAnimation: false,
                    data: [100]
                }, {
                    name: "",
                    type: "pie",
                    radius: [120, 135],
                    center: ["50%", "50%"],
                    label: {
                        normal: {
                            show: false
                        }
                    },
                    itemStyle: {
                        normal: {
                            shadowBlur: 10,
                            shadowColor: 'rgba(24,219,159,0.6)',
                        }
                    },
                    hoverAnimation: false,
                    hoverOffset: 5,
                    data: this.echartData
                }, {
                    name: "",
                    type: "pie",
                    radius: [0, 105],
                    center: ["50%", "50%"],
                    label: {
                        show: false
                    },
                    itemStyle: {
                        normal: {
                            color: new echarts.graphic.RadialGradient(.5, .5, .7, [{
                                    offset: 0.5,
                                    color: '#ffffff'
                                },
                                {
                                    offset: 1,
                                    color: '#18DB9F'
                                }
                            ], false)
                        },
                    },
                    hoverAnimation: false,
                    data: [100]
                }]
            };
            this.myChart = echarts.init(document.getElementById(this.score));
            this.myChart.setOption(option);
        }
    }
}
</script>>



