<template>
  <div class="resume-profile" dark>
    <v-row>
      <v-spacer></v-spacer>
      <v-avatar size='100'><img :src="logo" class="d-flex justify-center"></v-avatar>
      <v-spacer></v-spacer>
    </v-row>
    <h2 class="resume-name">{{ name }}</h2>
    <h3 class="resume-occupation">{{ profession }}</h3>

    <v-timeline align-top :dense="false">
      <v-timeline-item
        v-for="(item, i) in experience"
        :key="i" :color="item.lineColor"
        :icon="item.icon" fill-dot>
        <!-- <span slot="opposite">{{ item.time }}</span> -->
        <v-card :color="item.color" max-width="500" :style="i | cookMargin" >
          <v-img :height="item.size" width="100%" :src="item.img">
            <p class="resume-img-text text-sm-left">{{ item.imgName }}</p>
          </v-img>
          <v-card-text><b :style="{'color':item.titleColor}">{{ item.time }} -- {{ item.title }}</b></v-card-text>
          <v-card-text><p :style="{'color':item.descriptionColor}">{{ item.description }}</p></v-card-text>
        </v-card>
      </v-timeline-item>
    </v-timeline>
  </div>
</template>

<script>
import CONFIG from '@/config/personalInfo.js';

export default {
  name: 'personalProfile',
  data() {
    return {
      experience:CONFIG.experience,
      logo:CONFIG.logo,
      name:CONFIG.name,
      profession:CONFIG.profession,

    }
  },
  filters:{
    cookMargin(i){
      if(i%2 == 1){
        return "margin-left:8%"
      }
      return ""
    }
  }
}
</script>

<style lang="stylus" scoped>
.resume-profile
  position relative
  height  100%
  width 100%
  background-size cover
  background linear-gradient(rgb(69, 69, 70) 0%, rgb(0, 0, 0) 100%)

.resume-name
  color #00E5FF
  margin-top 20px
  text-align center

.resume-occupation
  color #64DD17
  margin-top 10px
  text-align center

.resume-img-text
  color #FF5252
  font-size 12px
  position relative
  top   5px
  right  0
</style>
