import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import About from '@/views/About.vue'
import layout from "@/views/layout.vue"
import wechat from '@/components/wechat.vue'
import FootPrint from '@/components/FootPrint.vue'
import projectList from '@/components/projectList.vue'
import resumeHome from '@/components/resume/resumeHome.vue'

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: '/',
    component:Home
  },
  {
    path: '/about-me',
    name: 'aboutMe',
    component:resumeHome
  },
  {
    path: '/blog',
    name: 'Blog',
    component: layout,
    redirect: '/blog/home',
    children: [
      {
        path: '/blog/home',
        name: 'home',
        component: projectList
      },
      {
        path: '/blog/traval',
        name: 'traval',
        component: FootPrint
      },
      {
        path: '/blog/wechat',
        name: 'wechat',
        component: wechat
      },
      {
        path: '/blog/time-line',
        name: 'time-line',
        component: About
      },
      {
        path: '/blog/tags',
        name: 'tags',
        component: About
      },
      {
        path: '/blog/notes',
        name: 'notes',
        component: About
      },
      {
        path: '/blog/secret',
        name: 'secret',
        component: About
      },
      {
        path: '/blog/solution',
        name: 'solution',
        component: About
      },
      {
        path: '/blog/collection',
        name: 'collection',
        component: About
      },
      
    ]
  },
  {
    path: '/projects',
    name: 'projects',
    children: [
      {
        path: '/love-talk',
        name: 'love-talk',
        component: About
      },
      {
        path: '/Maybe',
        name: 'Maybe',
        component: About
      },
    ]
  },
  {
    path: '/about-me',
    name: 'myself',
    component:resumeHome
  },
]

const router = new VueRouter({
  mode:"hash",
  routes:routes
})

export default router
