<template>
  <div class="resume-home-page wrapper">
    <v-card dark tile flat>
      <v-row>
        <v-col :md="2" class="resume-logo">
          <v-avatar dark size='36'><img :src="logo"></v-avatar>
          <span class="resume-logo-name">{{ author }}</span>
        </v-col>
        <v-col :md="2"></v-col>
        <v-col :md="5">
          <v-tabs @change="change">
            <v-tab key="personal" value="personal"><b>个人信息</b></v-tab>
            <v-tab key="skill" value="skill">个人技能</v-tab>
            <v-tab key="working" value="working">近期项目</v-tab>
            <v-tab key="contact" value="contact">联系方式</v-tab>
          </v-tabs>
        </v-col> 
        <v-spacer></v-spacer>
      </v-row>
    </v-card>
    <div v-if="tabsActive === 0" ref="profile"><personal-profile></personal-profile></div>
    <div v-if="tabsActive === 1" class="resume-content" ref="skill"><personal-skill></personal-skill></div>
    <div v-if="tabsActive === 2" class="resume-content" ref="experience"><work-experience></work-experience></div>
    <div v-if="tabsActive === 3" class="resume-content" ref="contact"><personal-contact></personal-contact></div>
  </div>
</template>

<script>
import CONFIG from '@/config/config.js';
import personalSkill from '@/components/resume/personalSkill.vue'
import workExperience from '@/components/resume/workExperience.vue'
import personalProfile from '@/components/resume/personalProfile.vue'
import personalContact from '@/components/resume/personalContact.vue'

export default {
  name: 'homePage',
  components: {
    personalSkill,
    workExperience,
    personalContact,
    personalProfile,
  },
  data() {
    return {
      tabsActive:0,
      scroll:null,
      location:0
    }
  },
  computed: { 
    author: function () {
      return CONFIG.AUTHOR
    },
    logo:function () {
      return CONFIG.LOGO
    }
  },
  methods: {
    change(value){
      this.tabsActive = value;
    },
  },
}
</script>

<style lang="stylus" scoped>
.resume-home-page
  top  0
  left 0
  right 0
  bottom  0
  width 100%
  height 100%
  position absolute
  font-family "Microsoft YaHei"

.resume-content
  width 100%
  height 100%

.resume-logo-name
  font-size   20px
  margin-left  8px
  font-family "LiSu", "Microsoft YaHei"

.resume-logo
  margin-left 2%
  margin-top  6px

</style>
